@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* FONT = MONTSERRAT */
 
html, body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

#login-page{width:100%;height:100vh;background-color:#0d0d0d}#login-page .container{padding-top:3rem;display:flex;flex-direction:column;align-items:center}#login-page .container header{display:flex;justify-content:center;align-items:center;margin-bottom:2rem}#login-page .container header img{width:100px;height:100px}#login-page .container header span{font-size:3rem;color:white;font-weight:bold}#login-page .container #login-fb{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);min-width:300px;color:white;background-color:#3b5998;margin-bottom:.5rem}#login-page .container #login-fb:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}#login-page .container .separator{display:flex;align-items:center;text-align:center;min-width:300px;text-transform:uppercase;font-size:.9rem;font-weight:600;color:white;margin-bottom:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{content:" ";flex:1 1;border-bottom:1px solid}#login-page .container .separator::before{margin-right:.5rem}#login-page .container .separator::after{margin-left:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{border-color:white}#login-page .container input{min-width:300px;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:none;padding:5px 1rem;font-size:.9rem;font-weight:600;margin-bottom:.75rem;outline:none;background-color:#282828;color:white}#login-page .container .remember{min-width:300px;display:flex;justify-content:space-between;color:white;font-weight:600;margin-bottom:1rem;position:relative}#login-page .container .remember .toast{top:-4rem;right:-90%}#login-page .container #login{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);background-color:#00d95f;color:white;min-width:150px}#login-page .container #login:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}

.toast{border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;position:absolute;border:white 1px solid;z-index:9999;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(0,0,0,0.6)}.toast .toast-header{background-color:inherit}.toast .toast-header button{border:none;background-color:transparent;font-weight:bold;color:white}.toast .toast-header button.close{display:none}.toast .toast-body{font-size:.6rem}.toggle-btn{width:50px;height:25px;border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:1px solid}.toggle-btn div{width:23px;height:23px;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;transition:margin-left 1s ease;-webkit-transition:margin-left 1s ease;-moz-transition:margin-left 1s ease;-ms-transition:margin-left 1s ease;-o-transition:margin-left 1s ease}.toggle-btn .toggle{background-color:white;cursor:pointer}.logo{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%)}.logo img{animation:scaleLogo 2s ease 1;-webkit-animation:scaleLogo 2s ease 1;transform-origin:50% 50%}@keyframes scaleLogo{0%{}100%{transform:scale(0.1);-webkit-transform:scale(0.1);-moz-transform:scale(0.1);-ms-transform:scale(0.1);-o-transform:scale(0.1)}}



